.photoGallery {
  width: 100%;
  display: grid;
  grid-template-rows: repeat(4, 180px);
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 5px;
}

.photo {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.photo:nth-child(1) {
  grid-row: 1 / 4;
  background-image: url(https://i.imgur.com/hkOjoryh.jpg);
}

.photo:nth-child(2) {
  grid-column: 2 / 4;
  grid-row: 1 / 3;
  background-image: url(https://i.imgur.com/aJ1eu9Uh.jpg);
}

.photo:nth-child(3) {
  grid-column: 2 / 3;
  grid-row: 3 / 4;
  background-image: url(https://i.imgur.com/ZkJEjIhl.jpg);
}

.photo:nth-child(4) {
  grid-column: 1 / 3;
  grid-row: 4 / 5;
  background-image: url(https://i.imgur.com/Z3UUO8N.jpg);
}

.photo:nth-child(5) {
  grid-column: 3 / 4;
  grid-row: 3 / 5;
  background-image: url(https://i.imgur.com/wT36r5ol.jpg);
}
