.footerContainer {
  width: 100%;
  background-color: var(--dark-90);
  color: white;
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 40px;
  align-items: center;
  p {
    font-size: 24px;
  }
  img{
    width: 40px;
    margin: 0 10px 5px 10px;
    cursor: pointer;
    &:hover{
      transform: scale(1.2)
    }
  }
  .right{
    font-size: 14px;
  }

}